import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";

class EstudioIndex extends Component {
  state = { devocionales: [], sermones: [], isAdminView: false };

  async componentDidMount() {
    const response = await axios.get("/_estudios");
    console.log(response);
    const editors = response.data.editors;
    const devocionales = editors.filter((editor) => editor.kind === "estudios");
    console.log("devocionales = ", devocionales);
    const sermones = editors.filter((editor) => editor.kind === "sermones");
    console.log("devocionales = ", devocionales);
    this.setState({
      devocionales: devocionales,
      sermones: sermones,
      isAdminView: response.data.isAdminView,
    });
  }

  createDevotionalEditor = () => {
    this.props.history.push("/estudios/new");
  };
  createSermonEditor = () => {
    this.props.history.push("/sermones/new");
  };

  render() {
    const devocionales = this.state.devocionales.map((editor, i) => {
      console.log("id = ", editor._id);
      const url = `/estudios/${editor._id}`;
      return (
        <li key={i} className="devocional-li">
          <Link to={url}>{editor.title}</Link>
        </li>
      );
    });
    const sermones = this.state.sermones.map((editor, i) => {
      const url = `/estudios/${editor._id}`;
      return (
        <li key={i} className="sermon-li">
          <Link to={url}>{editor.title}</Link>
        </li>
      );
    });

    return (
      <div className="">
        {this.state.isAdminView === true ? (
          <button
            onClick={this.createDevotionalEditor}
            className="btn btn-success mb-3 d-block"
          >
            Crear Nuevo Devocional
          </button>
        ) : (
          ""
        )}

        <div className="teaching-index">
          <h2>Devocionales</h2>
          <ul>{devocionales}</ul>
        </div>
        {this.state.isAdminView === true ? (
          <button
            onClick={this.createSermonEditor}
            className="btn btn-success mb-3 d-block"
          >
            Crear Nueva Predicacion
          </button>
        ) : (
          ""
        )}

        <div className="teaching-index">
          <h2>Predicaciones</h2>
          <ul>{sermones}</ul>
        </div>
      </div>
    );
  }
}

export default EstudioIndex;
